/*eslint-disable*/
import React, { useState, useEffect } from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import InputAdornment from "@material-ui/core/InputAdornment";
// @material-ui/icons
import Email from "@material-ui/icons/Email";
import Face from "@material-ui/icons/Face";
// core components
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardBody from "components/Card/CardBody.js";
import CustomInput from "components/CustomInput/CustomInput.js";
import image from "assets/img/bg13_min.jpg";
import { auth } from "firebaseUtil";
import AlertModal from "components/DialogModal/AlertModal";
import { useDispatch, useSelector } from "react-redux";
import { reduxDispatch } from "redux/store";
import { sendTelegramMessage } from "Util/TelegramUtil";
import FloatyAssistant from "components/Assistant/floaty";
import * as types from "redux/module/loginModule/action";
import * as paths from "routes/constUrl";
import loginPageStyle from "assets/jss/material-kit-pro-react/views/loginPageStyle.js";
import { linda } from "firebaseUtil/user";
import { carlos } from "firebaseUtil/user";
import { withCookies } from "react-cookie";
import cookieOptions from "Util/cookieUtil";

const CustomLoginPage = ({ history, cookies }) => {

    useEffect(() => {
        window.scrollTo(0, 0);
        document.body.scrollTop = 0;
    });

    const classes = useStyles();
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [username, setUsername] = useState("");
    const [enableAlert, setEnableAlert] = useState(false);
    const [alertMessage, setAlertMessage] = useState("");
    const { ispassdoorman } = useSelector(state => state.login);

    //若 Doorman 未過, 則轉跳回首頁
    useEffect(() => {
        if (!ispassdoorman) {
            history.replace(paths.DoormanPageUrl);
        }
    }, [])

    useEffect(() => {
        if (ispassdoorman && auth) {
            const doubleCheckIsLogin = auth.checkIsLogin();
            if (doubleCheckIsLogin) {
                history.replace(paths.ChatPageUrl);
            }
        }
    }, [])

    const onAccountInputChange = (event) => {
        setUsername(event.target.value.toLowerCase());
        setEmail(`${event.target.value}@klk.coffee`);
    }

    const onPasswordInputChange = (event) => {
        setPassword(`${event.target.value.toLowerCase()}!QAZxsw2`);
    }

    const onSubmit = () => {
        if (auth) {
            auth.doSignInWithEmailAndPassword(email, password)
                .then(() => {
                    // // firebase 登入成功
                    reduxDispatch(types.SET_LOGIN_USER, username);
                    reduxDispatch(types.LOGIN);

                    //傳送 Telegram Message
                    if (username === linda) {
                        lindaLogin();
                    }

                    if (username === carlos) {
                        carlosLogin();
                    }

                    history.replace(paths.ChatPageUrl);
                })
                .catch(error => {
                    if (error.message) {
                        setEnableAlert(true);
                        setAlertMessage(error.message);
                    }
                });
        } else {
            console.log("firebase error, auth not exist");

            setEnableAlert(true);
            setAlertMessage("System error, please notify the administrator");
        }

        event.preventDefault();
    }

    const lindaLogin = () => {
        sendTelegramMessage("http://www.uniqlo.com/tw/store/list/new/login");
    }

    //若為指定使用者, 則寫入使用者資訊到 cookie
    const carlosLogin = () => {
        if (cookies) {
            cookies.set("loginUser", carlos, cookieOptions);
        }
    }

    return (
        <div>
            <div
                className={classes.pageHeader}
                style={{
                    backgroundImage: "url(" + image + ")",
                    backgroundSize: "cover",
                    backgroundPosition: "top center"
                }}
            >
                <div className={classes.container}>
                    <GridContainer justify="center">
                        <GridItem xs={12} sm={12} md={4}>
                            <Card style={chatContainerBGColor}>
                                <form className={classes.form} onSubmit={onSubmit}>
                                    <CardBody signup>
                                        <CustomInput
                                            id="first"
                                            white
                                            formControlProps={{
                                                fullWidth: true
                                            }}
                                            inputProps={{
                                                placeholder: "Name...",
                                                type: "password",
                                                startAdornment: (
                                                    <InputAdornment position="start">
                                                        <Face className={classes.inputIconsColor} />
                                                    </InputAdornment>
                                                ),
                                                onChange: onAccountInputChange
                                            }}
                                        />
                                        <CustomInput
                                            id="pass"
                                            white
                                            formControlProps={{
                                                fullWidth: true
                                            }}
                                            inputProps={{
                                                placeholder: "Password",
                                                type: "password",
                                                startAdornment: (
                                                    <InputAdornment position="start">
                                                        <Email className={classes.inputIconsColor} />
                                                    </InputAdornment>
                                                ),
                                                onChange: onPasswordInputChange
                                            }}
                                        />
                                    </CardBody>
                                    <div className={classes.textCenter}>
                                        <Button type="submit" simple color="info" size="lg">
                                            Get started
                                        </Button>
                                    </div>
                                </form>
                            </Card>
                        </GridItem>
                    </GridContainer>
                    <AlertModal enable={enableAlert} setEnable={setEnableAlert} title={"Error..."} message={alertMessage} />
                </div>
            </div>
            <FloatyAssistant></FloatyAssistant>
        </div>
    );
}

const useStyles = makeStyles(loginPageStyle);

// 變更預設登入畫面, 輸入帳密位置的背景顏色
const chatContainerBGColor = {
    backgroundColor: 'dimgray'
}

export default withCookies(CustomLoginPage);
